export default "@conector-size: 16px;\n\n.editable-sample {\n  &-control {\n    margin-bottom: 10px;\n    min-height: 38px;\n  }\n  &-control-action {\n    float: left;\n  }\n  &-control-zoom {\n   float: left;\n   width: 320px;\n   margin-left: 30px;\n  }\n  .ant-slider {\n    min-width: 180px;\n  }\n  &-edge-label {\n    height: 100%;\n    width: calc(~\"100% - \" @conector-size);\n    margin: auto;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    &:hover {\n      background: rgba(0, 0, 255, 0.2);\n    }\n\n    div {\n      width: 16px;\n      height: 16px;\n    }\n  }\n  &-start-node {\n    &-move-hand {\n      position: absolute;\n      top: 0;\n      left: 0;\n      right: 0;\n      bottom: 0;\n    }\n  }\n  &-start-node,\n  &-end-node,\n  &-joint-node {\n    border: 1px solid black;\n    border-radius: 50%;\n    width: 30px;\n    height: 30px;\n    background: white;\n  }\n\n  &-joint-node {\n    &-move {\n      text-align: center;\n      padding-top: 2px;\n      z-index: 1;\n\n      .nice-dag-vue-sample-icon {\n        margin-left: 0;\n        margin-top: 4px;\n        font-size: 14px;\n      }\n    }\n\n    &-remove-button {\n      position: absolute;\n      top: -10px;\n      right: -2px;\n      background-color: white;\n      // border: 1px solid black;\n      height: 14px;\n      font-size: 12px;\n\n      .anticon-close {\n        transform: translateY(-2px);\n      }\n    }\n  }\n\n  &-end-node::before {\n    content: \"\";\n    position: absolute;\n    border: 1px solid black;\n    border-radius: 50%;\n    background: darkgray;\n    top: 5px;\n    left: 5px;\n    width: 20px;\n    height: 20px;\n  }\n\n  &-square-connector,\n  &-circle-connector {\n    &-in,\n    &-out {\n      width: @conector-size;\n      height: @conector-size;\n      border: 1px solid black;\n      border-radius: 50%;\n      position: absolute;\n      margin: auto;\n      top: 0;\n      bottom: 0;\n      background: white;\n    }\n\n    &-in {\n      left: @conector-size / -2;\n    }\n    &-out {\n      right: @conector-size / -2;\n      cursor: pointer;\n      z-index: 1;\n    }\n  }\n\n  &-square-connector {\n    &-in,\n    &-out {\n      border: 1px solid black;\n      border-radius: 0;\n    }\n  }\n\n  &-node {\n    width: 100%;\n    height: 100%;\n    background: white;\n\n    &-group-content,\n    &-content {\n      position: relative;\n      width: 100%;\n      height: 100%;\n      border: 1px solid #8799c1;\n      border-radius: 10px;\n      display: flex;\n      flex-direction: column;\n\n      &-delete-button {\n        position: absolute;\n        right: -10px;\n        top: -11px;\n        background: white;\n        font-size: 18px;\n        height: 20px;\n      }\n\n      &-bar {\n        cursor: pointer;\n        z-index: 2;\n        span:nth-child(2) {\n          margin-left: 4px;\n          font-size: 16px;\n        }\n      }\n    }\n    &-group-content {\n      padding: 4px 20px;\n      > div {\n        height: 30px;\n\n        a {\n          margin-left: 6px;\n          font-size: 16px;\n        }\n      }\n      &-bar {\n        cursor: pointer;\n        display: inline-block;\n        span:nth-child(2) {\n          margin-left: 4px;\n          font-size: 16px;\n        }\n      }\n    }\n    &-content {\n      align-items: center;\n      justify-content: center;\n    }\n  }\n\n  &-content-nice-dag {\n    height: 400px;\n    border: 1px solid #efefef;\n  }\n}\n";