export default "<template>\n  <div\n    :class=\"`editable-sample-${shape || 'circle'}-connector-${type}`\"\n    @mousedown=\"startEdgeDragging\"\n  />\n</template>\n\n<script>\nexport default {\n  name: \"EditableConnector\",\n  props: [\"node\", \"niceDag\", \"type\", \"shape\"],\n  setup(props) {\n    return {\n      startEdgeDragging(e) {\n        if (props.node) {\n          props.niceDag.startEdgeDragging(props.node, e);\n        }\n      },\n    };\n  },\n};\n</script>\n";