export default "@conector-size: 16px;\n\n.readonly-sample {\n  border: 1px solid #eaeaea;\n  padding: 4px 4px;\n  &-control {\n    margin-bottom: 10px;\n    min-height: 38px;\n  }\n  &-control-direction {\n    float: left;\n  }\n  &-control-zoom {\n    float: left;\n    width: 400px;\n    margin-left: 30px;\n   }\n  &-minimap {\n    width: 250px;\n    height: 200px;\n  }\n  &-edge-label {\n    text-align: center;\n    margin-top: -20px;\n  }\n  &-start-node,\n  &-end-node,\n  &-joint-node {\n    border: 1px solid black;\n    border-radius: 50%;\n    width: 30px;\n    height: 30px;\n    background: white;\n  }\n\n  &-joint-node {\n    font-size: 18px;\n    display: flex;\n    align-content: center;\n    justify-content: center;\n    align-items: center;\n  }\n\n  &-end-node::before {\n    content: \"\";\n    position: absolute;\n    border: 1px solid black;\n    border-radius: 50%;\n    background: black;\n    top: 5px;\n    left: 5px;\n    width: 20px;\n    height: 20px;\n  }\n\n  &-connector {\n    &-in,\n    &-out {\n      width: @conector-size;\n      height: @conector-size;\n      border: 1px solid black;\n      border-radius: 50%;\n      position: absolute;\n      margin: auto;\n      top: 0;\n      bottom: 0;\n      background: white;\n    }\n\n    &-in {\n      left: @conector-size / -2;\n    }\n    &-out {\n      right: @conector-size / -2;\n    }\n  }\n\n  &-node {\n    width: 100%;\n    height: 100%;\n    &-group-content,\n    &-content {\n      position: relative;\n      width: 100%;\n      height: 100%;\n      border: 1px solid #999999;\n      border-radius: 10px;\n      display: flex;\n      flex-direction: column;\n      box-sizing: border-box;\n    }\n    &-group-content {\n      padding: 4px 20px;\n      > div {\n        height: 30px;\n\n        a {\n          margin-left: 6px;\n          font-size: 16px;\n        }\n      }\n    }\n    &-content {\n      align-items: center;\n      justify-content: center;\n    }\n  }\n\n  &-content {\n    height: 400px;\n    position: relative;\n    &-nice-dag {\n      height: 100%;\n    }\n    &-minimap-row {\n      height: 130px;\n      position: relative;\n    }\n    &-minimap {\n      position: absolute;\n      top: 0;\n      right: 0;\n      width: 300px;\n      height: 120px;\n      bottom: 20px; //scroll bar\n      border: 1px solid gray;\n      padding: 2px 2px;\n      background-color: #efefef;\n\n      &-body {\n        width: 100%;\n        height: 100%;\n      }\n    }\n  }\n  &-minimap-viewbox {\n    border: 1px solid orange !important;\n  }\n}";